.react-bootstrap-table th[data-row-selection] {
  width: 120px;
}

.expanded-row {
  background-color: #20a8d8 !important;
  color: #ffffff;
}

.expanded-row:hover {
  color: #ffffff !important;
}

.expanded-row-area {
  max-height: 150px;
  overflow-y: auto;
}

ins {
  color: #0000ff;
  font-weight: bold;
}

del {
  color: #ff0000;
  font-weight: 700;
}

.blue-square {
  background-color: #0000ff;
  width: 10px;
  height: 10px;
  margin-right: 6px;
}

.red-square {
  background-color: #ff0000;
  width: 10px;
  height: 10px;
  margin-right: 6px;
}
